export default {
	data() {
		return {
			shadow: false,
			animateShow: {},
			navH: 0,
			clearH:0,
			userInfo:{},
			wxImg:[
				'../../static/code.jpg',
			],
			wx:'../../static/code.jpg',
		}
	},
	onLoad() {
		this.navHeight();
		this.getUser();
	},
	onShow() {
		// 初始化一个动画
		var animation = uni.createAnimation({
			// duration: 2000,
			timingFunction: 'ease',
		})
		this.animation = animation
	},
	methods: {
		showShadow() {
			this.shadow = true;
			//this.opacityAnimate();
		},
		closeShadow() {
			this.shadow = false;
			//this.closeAnimate();
		},
		opacityAnimate() {
			this.animation
				.opacity(0).scale(0.5, 0.5).step({
					duration: 200
				})
				.opacity(1).scale(1, 1).step({
					duration: 500
				});

			// 导出动画数据传递给data层
			this.animateShow = this.animation.export();
		},
		closeAnimate() {
			this.animation
				.opacity(1).step({
					duration: 500
				})
				.opacity(0).step({
					duration: 500
				});

			// 导出动画数据传递给data层
			this.animateShow = this.animation.export();
		},
		
		//获取不同手机导航栏高度
		navHeight() {
			uni.getSystemInfo({
				success: (res) => {
					if(res.model == "iPhone XR" ||res.model == "iPhone X" ||res.model == "iPhone XS Max"){
						this.navH = res.statusBarHeight * 2  + (res.screenHeight - res.windowHeight) ;
						this.clearH = res.statusBarHeight * 2 + (res.screenHeight - res.windowHeight);
					}else{
						this.navH = res.statusBarHeight * 3  + (res.screenHeight - res.windowHeight) ;
						this.clearH = res.statusBarHeight * 3 + (res.screenHeight - res.windowHeight);
					}
					
					// console.log(res);

				},
				fail: (err) => {
					console.log(err);
				}
			})

		},
		
		getUser(){
			uni.getUserInfo({
				success: (res) => {
					this.userInfo = res.userInfo;
				}
			})
		},
		//获取用户信息和openid
		getUserInfo(){
			uni.login({
				success:res =>{
					if(res.code){
						uni.getUserInfo({
							withCredentials:true,
							success: (res_user) => {
								this.getOpenId(res.code,res_user.encryptedData,res.iv);
								this.userInfo = res_user.userInfo;
								// console.log(res_user);
								uni.navigateTo({
									url: '/pages/user/user'
								});
								uni.setStorage({
									key:"userInfo",
									data:res_user.userInfo
								})
							},
							
						})
					}else{
						
					}
				}
			})
		},
		
		async getOpenId(code,encryptedData,iv){
			var data = {
				// appid:"wxddfdc0207f9685cc",
				// secret:"46c39d9ef7186ded35c917314b298070",
				 js_code:code,
				// grant_type:"authorization_code",
				// encryptedData:encryptedData,
				// iv:iv
			};
			// console.log(code);
			// var res = await this.$request.get('http://127.0.0.1:5000/getUser',data);
			var res = await this.$request.get("https://api.weixin.qq.com/sns/jscode2session?appid=wxddfdc0207f9685cc&secret=46c39d9ef7186ded35c917314b298070&js_code=" + code + "&grant_type=authorization_code",);
			console.log(res);
			var openid = res.data.openid;
			uni.setStorage({
				key:'openid',
				data:res.data.openid
			})
		},
		
		
		//
		onShareAppMessage() {
			return {
				title: '合肥软界信息科技有限公司',
				path: '/pages/index/index',
			}
		},
		
		preImg(){
			 // var src = e.currenttarget.dataset.src;
			uni.previewImage({
				current: this.wx,
				urls:[this.wx],
				success: (res) => {
					console.log(res)
				},
				fail: (err) => {
					console.log(err)
				}
			})
		},
		saveImg () {
		    uni.getSetting({
		      success:(res)=> {
		        //未授权 先授权 然后保存
		        if (!res.authSetting['scope.writePhotosAlbum']) {
		          uni.authorize({
		            scope: 'scope.writePhotosAlbum',
		            success:(re)=> {
		              this.saveToBlum();
		            }
		          })
		        }else{
		         //已授 直接调用保存到相册方法
		          this.saveToBlum();
		        }
		      }
		    })  
		},
		saveToBlum(){

			    wx.getImageInfo({
			      src: this.wx,
			      success:(ret)=> {
			        var path = ret.path;
			        wx.saveImageToPhotosAlbum({
			          filePath: path,
			          success:(result)=> {
			            wx.showToast({
			              title: '保存成功',
			              icon: 'success'
			            })
			          },
			        })
			      }
			    })
		}
	},
}
